.list {
  font-family: "Fira Sans", sans-serif;
  background-image: radial-gradient(
      circle,
      rgba(3, 5, 5, 0.917) 35%,
      rgba(90, 230, 240, 0.292) 100%
    ),
    url("../images/background.jpg");
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  column-gap: 20px;
  row-gap: 20px;
  justify-items: center;
  padding: 20px;

  &__article {
    background: rgb(3, 5, 5);
    background: radial-gradient(
      circle,
      rgba(3, 5, 5, 1) 43%,
      rgba(90, 231, 240, 1) 100%
    );
    padding: 20px;
    border-radius: 10%;
    color: white;
    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 250px;
    height: 330px;
  }

  &__img {
    width: 200px;
  }

  &__name {
    font-size: 1.3rem;
  }

  &__icon {
    height: 30px;
    margin-bottom: 10px;
  }
}
